@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.geogebra{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.showcase{
    background: url(/labvirtual/static/media/gedung_d10.fd72ed10.jpeg) no-repeat center/cover;
    height: 700px;
}

.showcase .overlay{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 700px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.4);
}

.showcase .overlay h1{
    font-size: 36px;
}
.showcase .overlay h3{
    font-size: 20px;
}
.showcase .overlay h5{
    font-size: 18px;
}
.showcase .overlay p{
    font-size: 18px;
}


.grid {
    padding:  50px;
}
.grid h3{
    text-align: center;
    font-size: 30px;
    color: #333;
    padding-bottom: 20px;
}
.grid .grid-items div{
    background-color: #f1f1f1;
    margin:20px;
    padding:20px;
    border-radius: 10px;
    text-align: center;
}

.grid .grid-items div img {
    border-radius: 10px;
    width: 100%;
}

.grid .grid-items div h4{
    padding: 10px 0;
    font-size: 22px;
}

.grid .grid-items div p{
    line-height: 1.8;
}


.footer {
    background-color: #00B4D8;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer h5{
    color: #fff;
    font-size: 20px;
}
.footer p {
    color: #fff;
    font-size: 15px;
}


@media(min-width: 768px){
    .showcase{
        background-position: top;
    }
    .showcase .overlay h1{
        font-size: 64px;
    }
    .showcase .overlay h3{
        font-size: 40px;
    }
    .showcase .overlay h5{
        font-size: 36px;
    }
    .showcase .overlay p{
        font-size: 36px;
    }
    
    .grid .grid-items{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px;
        gap: 30px;
    }
    .grid .grid-items div img {
        width: 100%;
    }
}

.geogebra-iframe{
    width: 100%;
    align-items: center;
    justify-content: center;
}
.geogebra-iframe iframe{
    margin-top: 90px;
    width:100%;
    height: 90vh;
}
.geogebra Iframe{
    margin-top: 90px;
}

